@keyframes title-anim {
  0% {
    transform: scale(1.5);
    -webkit-transform: scale(1.5);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes title-anim {
  0% {
    transform: scale(1.5);
    -webkit-transform: scale(1.5);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes talk-anim {
  0% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
@keyframes talk-anim {
  0% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
.page3 {
  background: #fff;
  width: 100%;
  position: absolute;
  z-index: 10;
}
.page3 .title-scale-anim {
  animation: title-anim 1s;
  -webkit-animation: title-anim 1s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
.page3 .talk-scale-anim {
  animation: talk-anim 1s;
  -webkit-animation: talk-anim 1s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
