.wrapper {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  overflow: hidden;
}
.page-list {
  position: relative;
}
.page {
  background: #ffffff;
  width: 100%;
  position: absolute;
  font-size: 2rem;
  z-index: 10;
}
.touch-arrow {
  animation: touchstyle-arrow-animate 2s infinite;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  margin-left: 15px;
  width: 30px;
}
.logo-360 img {
  position: fixed;
  left: 20px;
  top: 20px;
  z-index: 999;
  display: none;
}
.logo-xy img {
  position: fixed;
  right: 20px;
  bottom: 18px;
  z-index: 999;
  display: none;
}
.music img {
  position: fixed;
  right: 61px;
  top: 127px;
  z-index: 999;
  display: none;
}
.music.play img {
  -webkit-animation: music_play 4s linear infinite;
}
@-webkit-keyframes music_play {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
.pending {
  font-size: 2rem;
}
